.Course{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 1200px;
    .first{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 60%;
        text-align: center;
        h2{
            color: #1e4854;
            font-size: 2rem;
            margin-bottom: 20px;
        }
        p{
            font-size: 1rem;
        }

    }  
    .second{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 40px;
        .card{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            height: 400px;
            width: 30%;
            border: 0.5px solid #e6e5e5;
            -webkit-box-shadow: 0px 0px 73px -35px rgba(20, 21, 27);
            -moz-box-shadow: 0px 0px 73px -35px rgba(20, 21, 27);
            box-shadow: 0px 0px 73px -35px rgb(20, 21, 27);
            
            img{
                width: 100%;
                height: 250px;
            }
            .text{
                height: 60%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                h3{
                    margin-top: 1rem;
                    font-size: 1.2rem;
                }
                p{
                    margin-top: 1.5rem;
                    text-align: center;
                }
                button{
                    width: 80%;
                    height: 30px;
                    border-radius: 10px;
                    border: none;
                    background-color: #1820fa;
                    color: white;
                    cursor: pointer;
                }
            }
        }
    } 
}