body {
  margin: 0 auto;
  padding: 0;
}
/**
* resposive design
*/
@media (max-width: 1201px) {
  .App {
    margin: 0;
    padding: 0;
    width: 1190px;
    .PLayout {
      width: 1190px;
      .containerFluidNavBar {
        width: 100%;
        height: 60px;
      }
      .homeContainer {
        width: 100%;
        .IntroBanner {
          width: 100%;
          top: 80px;
          height: 80vh;
          .information{
            width: 80%;
            h1{
              text-align: center;
              font-size: 1.8rem;
            }
            p{
              font: 0.5rem;
            }
          }
        }
        .AboutUS {
          width: 100%;
          .containerAboutUs {
            .card {
              top: -70px;
            }
          }
        }
        .Course {
          width: 100%;
        }
        .containerProfessor {
          width: 100%;
        }
        .containerTestimonial {
          width: 100%;
          .AvisSpace {
            .cardAvis {
              width: 40%;
              .student {
                img {
                }
              }
            }
          }
        }
        .containerContactUs {
          width: 100%;
        }
      }
      .containerLogin {
        .loginComponent {
          top: 20px;
          width: 30%;
          height: 80%;
          .titleLogin {
            font-size: 1.5rem;
          }
          .form {
            height: 90%;

            .groupInput {
              width: 85%;
              margin-top: 1rem;
              .input {
                .iconLogin {
                  font-size: 1rem;
                }
                input {
                  height: 2.5rem;
                }
              }
            }
            .btnConnexion {
              margin-top: 5rem;
              height: 40%;
            }
          }
        }
      }
      .contactContainer{
        .secondComponent{
          margin-top: 4rem;
          .aside{
            width: 35%;
            height: 90%;
            .introduction {
              h3{
                font-size: 1.3rem !important;
              }
              p{
                font-size: 0.8rem;
              }
            }
            .contactEPA{
              margin-top: 2rem;
              p{
                font-size: 1rem;
              }
            }
          }
        }
      }
    }
  }
}
@media(max-width: 480px){
  .App{
    visibility: hidden;
  }
}
